	module.exports = "<div class=\"home\">\n  <section class=\"home-header\">\n    <h1 class=\"title\">The Angular2 & Apollo Client Starter Kit</h1>\n    <h2>A GraphQL powered Angular2 application ready to be made awesome.</h2>\n    <div class=\"img-container\">\n      <a target=\"_blank\" href=\"https://angular.io\"><img src=\"https://angular.io/resources/images/logos/standard/shield-large.png\" alt=\"Angular2\"></a>\n      <a target=\"_blank\" href=\"https://scaphold.io\"><img src=\"https://scaphold.io/images/scaphold-logo-90x90.png\" alt=\"Scaphold\"></a>\n      <a target=\"_blank\" href=\"http://docs.apollostack.com/apollo-client/index.html\"><img src=\"http://docs.apollostack.com/logo/square.png\" alt=\"Apollo Client\"></a>\n    </div>\n  </section>\n  <section class=\"section-auth\">\n    <div *ngIf=\"!auth.credential.token\" class=\"authentication-container\">\n      <td>\n        <scaphold-register-form></scaphold-register-form>\n      </td>\n      <td>\n        <scaphold-login-form></scaphold-login-form>\n      </td>\n    </div>\n    <md-card *ngIf=\"auth.credential.token\">\n      <md-card-title>\n        Logged in as {{auth.user.username}}\n      </md-card-title>\n      <md-card-actions>\n        <button (click)=\"auth.logout()\" md-button>Logout</button>\n      </md-card-actions>\n    </md-card>\n  </section>\n  <section class=\"pagination\">\n    <md-card>\n      <md-card-title>Pagination with Users</md-card-title>\n      <md-card-content>\n        <md-input placeholder=\"Default Page Size\" [(ngModel)]=\"defaultPageSize\"></md-input>\n        <md-input placeholder=\"First\" [(ngModel)]=\"first\"></md-input>\n        <md-input placeholder=\"After\" [(ngModel)]=\"after\"></md-input>\n        <md-input placeholder=\"Last\" [(ngModel)]=\"last\"></md-input>\n        <md-input placeholder=\"Before\" [(ngModel)]=\"before\"></md-input>\n      </md-card-content>\n      <md-card-actions>\n        <button md-button (click)=\"getPreviousPageOfUsers()\" class=\"btn\">Previous</button>\n        <button md-button (click)=\"getNextPageOfUsers()\" class=\"btn\">Next</button>\n      </md-card-actions>\n    </md-card>\n  </section>\n  <section class=\"section-content\">\n    <div *ngIf=\"data.viewer && data.viewer.allUsers\">\n      <div *ngFor=\"let edge of data.viewer.allUsers.edges\">\n        <md-card>\n          <md-card-title>{{edge.node.username}}</md-card-title>\n          <md-card-content>\n            <div>Cursor: {{edge.cursor}}</div>\n            <div>Last Login: {{edge.node.lastLogin}}</div>\n            <div>Created At: {{edge.node.createdAt}}</div>\n          </md-card-content>\n        </md-card>\n        <!--<div class=\"panel\">\n        </div>-->\n      </div>\n    </div>\n  </section>\n</div>\n"

